<template>
  <div>
    <div v-for="item in datas">
      <Link :appName="item.appName" :href="item.href" :icon="item.icon" />
    </div>
    <button @click="create">创建</button>
  </div>
</template>
<script>
import Link from '@/components/Link.vue'
export default {
  data() {
    return {
      datas: [
        {
          appName: 'aa',
          href: 'https://www.fxsound.com/',
          icon: 'https://global-uploads.webflow.com/5d9ee27d931ebcfda8f814d3/5f63ded9b9dd5a595320fc01_256.png',
        },
        {
          appName: 'bb',
          href: 'https://www.fxsound.com/',
          icon: 'https://global-uploads.webflow.com/5d9ee27d931ebcfda8f814d3/5f63ded9b9dd5a595320fc01_256.png',
        },
      ],
    }
  },
  components: {
    Link,
  },
  methods: {
    create() {
      let obj = {
        appName: this.items.appName,
        href: this.items.href,
        icon: this.items.icon,
      }
      this.datas.push(obj)
      this.num++
      //将一个或多个值添加到数组的开头(前置)
      this.items.unshift({
        icon: '',
        appName: '',
        href: '',
      })
    },
  },
}
</script>
